import type { XtxGuessInstance } from '@/types/component'
import { ref } from 'vue'

export const useGuessList = () => {
  const guessRef = ref<XtxGuessInstance>()
  //滚动触底的事件
  const onScrolltoLower = () => {
    guessRef.value?.getMore()
  }
  return {
    guessRef,
    onScrolltoLower,
  }
}
